@font-face {
  font-family: MarioKart;
  src: url("Mario-Kart-DS.51c4cac3.ttf");
  font-weight: bold;
}

* {
  background-color: #f1f7ed;
  margin: 0;
  padding: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 18px;
}

main {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
  display: flex;
}

h1 {
  text-align: center;
  font-size: 3rem;
}

#app {
  padding: 1rem;
  font-family: MarioKart;
}

#instructions {
  background-color: #e3d081;
  border: .3rem solid #54494b;
  border-radius: 1rem;
  flex-direction: column;
  padding: 1rem;
  display: flex;
}

.indiv-bin {
  border: .3rem solid #54494b;
  border-radius: 1rem;
  flex-direction: column;
  width: -moz-fit-content;
  width: fit-content;
  margin: .5rem;
  padding: .5rem;
  display: flex;
}

.indiv-bin h3 {
  font-size: larger;
}

.indiv-bin-img {
  align-items: center;
  display: flex;
}

.bin-img, .trash-img {
  width: 40px;
}

#bin-list {
  background-color: #e3d081;
  justify-content: center;
  display: flex;
}

#description h2 {
  text-align: center;
  color: #b33951;
  background-color: #e3d081;
  padding: .3rem;
  font-size: xx-large;
}

#description p {
  background-color: #e3d081;
}

#inputBox {
  border: none;
  padding: .5rem;
  position: absolute;
  top: 500px;
}

footer {
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  text-decoration: none;
  display: flex;
}

